@import "./variables_background_images";
@import "./variables_color";

@mixin allBackgrondImagesSizing {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 100vh;
  width: 100%;
}
@mixin background_image_home {
  background-image: $home_background;
  @include allBackgrondImagesSizing;
}

@mixin easeOut {
  transition: all 1s ease-out;
}

@mixin btn_buy {
  margin-left: 10px;
  padding: 2px;
  background: $primery_color;
  font-size: 12px;
  text-transform: uppercase;
  font-family: "Monda", sans-serif;
  border: 1px solid black;
  border-radius: 3px;
  color: black;
  text-decoration: none;
  font-weight: bold;
  cursor: pointer;

  &:hover {
    color: black;
    background-color: $btnBuyPC;
  }
}

@mixin btn_back {
  color: black;
  display: inline-block;
  text-decoration: none;
  font-weight: bold;
  font-size: 15px;
  padding: 7px;
  background: $header_background;
  border-radius: 3px;

  span {
    font-family: "Monda", sans-serif;
  }
}
@mixin sizingBody {
  margin: 1rem auto;
  max-width: 1200px;
  height: auto;
  background-color: white;
  border-radius: 5px;
  min-height: calc(100vh - 20px);
  padding: 1rem;
}

@mixin nav_menu_components_phone_and_tablet {
  .big_box_components {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin: 0 2rem;
    border-radius: 0 0 5px 5px;
    background: $primery_color;
    .navProductsParrent {
      position: relative;
      height: auto;
      width: 220px;
      .link_menu {
        text-align: center;
        a {
          text-decoration: none;
          color: black;
          font-family: "Teko", sans-serif;
          font-size: 23px;
        }
      }
      .drop_down_menu_items {
        display: none;
        li {
          list-style: none;
          a {
            float: none;
            color: $header_links_color;
            text-decoration: none;
            padding: 10px;
            display: block;
            text-align: left;
            padding-left: 10px;

            .fas {
              pointer-events: none;
              position: absolute;
              right: 3px;
              top: 9px;
              color: $header_background;
              font-size: 15px;
            }
          }
        }
        .drop_down_menu_items_item {
          display: none;
        }
        .drop_down_menu_items_item {
          display: block;
          .btn_items_item {
            font-family: "Teko", sans-serif;
            width: 100%;
            color: $header_links_color;
            font-size: 18px;
            text-align: left;
            border: none;
            background: rgb(53, 52, 52);
            padding: 4px 0 0 15px;

            cursor: pointer;
            &:hover {
              background: rgba(176, 221, 233, 0.904);
            }
          }
        }
      }
      .drop_down_menu_items {
        border: 1px solid $border;
        border-radius: 3px;
        display: block;
        display: flex;
        flex-direction: column;
        height: auto;
        margin-bottom: 0.1rem;

        &:last-child {
          border-radius: 0 0 5px 5px;
        }
        a {
          font-family: "Monda", sans-serif;
          font-size: 15px;
          &:hover {
            background: $dropDownMenu_color;
            color: $header_links_color;
          }
        }
      }
    }
  }
}
@mixin nav_menu_components_laptop_and_desktop {
  .big_box_components {
    display: flex;
    justify-content: space-evenly;
    margin: 0 2rem;
    border-radius: 0 0 5px 5px;
    background: $primery_color;
    .navProductsParrent {
      height: 40px;
      width: 220px;

      .link_menu {
        text-align: center;
        margin-top: 3px;
        padding-top: 4px;
        a {
          text-decoration: none;
          color: black;
          font-family: "Teko", sans-serif;
          font-size: 23px;
        }
      }
      .drop_down_menu_items {
        // display: none;
 
        li {
          list-style: none;
          a {
            color: $header_links_color;
            text-decoration: none;
            padding: 10px;
            display: block;
            text-align: left;
            padding-left: 30px;
            .fas {
              position: absolute;
              right: 3px;
              top: 17px;
              color: $header_background;
              font-size: 12px;
            }
          }
        }
        .drop_down_menu_items_item {
          display: none;
        }
        .drop_down_menu_items_item {
          display: block;
          .btn_items_item {
            font-family: "Teko", sans-serif;
            width: 100%;
            color: $header_links_color;
            font-size: 18px;
            border: none;
            text-align: left;
            background: white;
            padding: 4px 0 0 15px;
            cursor: pointer;
            &:hover {
              background: rgba(176, 221, 233, 0.329);
            }
          }
        }
      }
      .drop_down_menu_items {
        background: white;
        position: relative;
        display: block;
        height: auto;
        border-left: 1px solid rgba(128, 128, 128, 0.267);
        border-right: 1px solid rgba(128, 128, 128, 0.267);
        border-bottom: 1px solid rgba(128, 128, 128, 0.267);
        &:last-child {
          border-radius: 0 0 5px 5px;
        }
        a {
          font-family: "Monda", sans-serif;
          font-size: 15px;
          &:hover {
            background: white;
            color: $header_links_color;
          }
        }
      }
    }
  }
}
@mixin current_component_clicked_on_parrent {
  .current_component_clicked_on_parrent {
    background: white;
    border-radius: 5px;
    font-family: "Teko", sans-serif;
    padding: 0 1rem;
    //opacity: 0.9;
    .current_componentTop {
      //@at-rootborder: 2px solid red;
      height: auto;
      display: flex;
      justify-content: space-between;
      padding: 1rem;

      .name_and_btnReadMore {
        display: flex;
        align-items: flex-end;

        span {
          //border: 1px solid red;
          font-size: 26px;
          font-family: "Monda", sans-serif;
          font-weight: bold;
        }
        .read_more_button {
          //border: 1px solid red;
          margin: 0 0 6px 1rem;
          text-decoration: none;
          text-transform: uppercase;
          font-size: 12px;
          font-family: "Monda", sans-serif;
          color: $header_background;
          &:hover {
            color: $header_links_color;
          }

          .fas {
            margin-left: 3px;
          }
        }
      }

      .ordeyBy {
        //border: 1px solid blue;
        background-color: $bodyColor;
        border-radius: 2px;
        height: 30px;
        width: 150px;
        // z-index: 999;
        .drop_down_ordeyBy {
          width: 150px;
          border: none;
          padding: 2px;
          &:first-child {
            background-color: $bodyColor;
            font-family: "Monda", sans-serif;
            font-size: 13px;
            padding-left: 5px;
          }
        }
      }
    }
    .show_readMore_info {
      //border: 1px solid red;
      padding: 0 0 1rem 1rem;
      height: auto;
      font-size: 15px; 
      width: 70%;
      color: $header_links_color;
      font-family: "Monda", sans-serif;
      p {
        margin-top: 0.5rem;
      }
      a {
        font-weight: bold;
        color: purple;
        font-size: 15px;
      }
    }
 
    .childs_product {
      //border: 1px solid red;
      display: flex;
      justify-content: space-between;
      border-top: 1px solid rgba(128, 128, 128, 0.192);
      padding: 0.5rem 0;

      .left_side_childs_product {
        width: 50%;
        display: flex;
        justify-content: space-between;
        align-items: center;

        .child_img {
          //border: 1px solid blue;
          width: 100px;
          img {
            width: 80px;
            height: 80px;
          }
        }
        .child_name {
          //border: 1px solid blue;
          display: flex;
          flex-direction: column;
          width: 70%;
          span {
            color: purple;
            font-weight: bold;
          }
          a {
            text-decoration: none;
            color: $header_links_color;

            &:hover {
              //border-bottom: 1px solid rgba(128, 128, 128, 0.616);
            }
          }
          .customer_review {
            //border: 1px solid red;
            color: rgba(128, 128, 128, 0.747);
            font-size: 14px;

            .fas {
              color: #f29312;
              margin-left: 2px;
              font-size: 11px;
            }
          }
        }
      }
      .right_side_childs_product {
        width: 50%;
        display: flex;
        justify-content: space-around;
        align-items: center;

        .child_price {
          //border: 1px solid blue;
          width: 150px;
          height: 100px;

          h4 {
            // border: 1px solid red;
            font-size: 12px;
            margin-top: 2rem;
            color: $header_background;
            span {
              font-size: 15px;
            }
          }
          h6 {
            color: $header_background;
            span {
              color: $header_links_color;
            }
          }
        }
        .child_btnAddToCart {
          text-align: center;
          //border: 1px solid blue;
          width: 100px;
          height: 100px;
          .wish_list {
            margin: 1px auto;
            button {
              border-radius: 50px;
              width: 30px;
              height: 30px;
              border: 1px solid rgba(128, 128, 128, 0.274);
              background: white;
              cursor: pointer;
              .fas {
                color: rgba(128, 128, 128, 0.637);
              }
              &:hover {
                .fas {
                  color: rgb(128, 128, 128);
                }
              }
            }
          }
          .btnCart {
            button {
              background: $btnBuyPC;
              border-radius: 2px;
              width: 90px;
              margin: 2px auto;
              height: 30px;
              padding: 2px;
              border: none;
              cursor: pointer;
              font-family: "Monda", sans-serif;
              color: white;
              &:hover {
                background: rgba(71, 136, 70, 0.815);
              }
            }
          }
          span {
            color: $btnBuyPC;
            font-family: "Monda", sans-serif;
            font-size: 12px;
            text-transform: uppercase;
            .fas {
              margin-right: 2px;
              color: rgba(128, 128, 128, 0.637);
            }
          }
        }
      }
    }
  }
}
